import React, { useState, useEffect } from 'react';
import axios from 'axios';

interface User {
  id: number;
  username: string;
  role: 'manager' | 'team_lead' | 'employee';
  email: string;
  available_hours_per_week?: number;
}

const Users: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [showAddForm, setShowAddForm] = useState(false);
  const [showEditForm, setShowEditForm] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [newUser, setNewUser] = useState({
    username: '',
    password: '',
    role: '',
    email: '',
    available_hours_per_week: 40
  });

  useEffect(() => {
    fetchUsers();
  }, []);

  const fetchUsers = async () => {
    try {
      const response = await axios.get('http://72.60.101.240:5005/api/users');
      setUsers(response.data);
    } catch (error) {
      console.error('Error fetching users:', error);
    }
  };

  const handleAddUser = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      // Create user account only
      await axios.post('http://72.60.101.240:5005/api/users', {
        username: newUser.username,
        password: newUser.password,
        role: newUser.role,
        email: newUser.email,
        available_hours_per_week: newUser.available_hours_per_week
      });

      setNewUser({ username: '', password: '', role: '', email: '', available_hours_per_week: 40 });
      setShowAddForm(false);
      fetchUsers();
    } catch (error) {
      console.error('Error adding user:', error);
    }
  };

  const handleEditUser = (user: User) => {
    setEditingUser(user);
    setShowEditForm(true);
  };

  const handleUpdateUser = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editingUser) return;

    try {
      await axios.put(`http://72.60.101.240:5005/api/users/${editingUser.id}`, {
        username: editingUser.username,
        email: editingUser.email,
        role: editingUser.role,
        available_hours_per_week: editingUser.available_hours_per_week
      });

      setShowEditForm(false);
      setEditingUser(null);
      fetchUsers();
    } catch (error) {
      console.error('Error updating user:', error);
      alert('Failed to update user');
    }
  };

  const handleDeleteUser = async (userId: number) => {
    if (window.confirm('Are you sure you want to delete this user?')) {
      try {
        await axios.delete(`http://72.60.101.240:5005/api/users/${userId}`);
        fetchUsers();
      } catch (error) {
        console.error('Error deleting user:', error);
        alert('Failed to delete user');
      }
    }
  };

  const handleExportUsers = () => {
    const csvContent = [
      ['Username', 'Email', 'Role', 'Available Hours/Week'],
      ...users.map(user => [
        `"${user.username}"`,
        `"${user.email}"`,
        `"${user.role}"`,
        `"${user.available_hours_per_week || 40}"`
      ])
    ].map(row => row.join(',')).join('\n');

    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'users_export.csv';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  };

  const handleImportUsers = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = async (event) => {
      const csv = event.target?.result as string;
      const lines = csv.split('\n').filter(line => line.trim());
      
      for (let i = 1; i < lines.length; i++) {
        // Parse CSV line properly (handle quoted values)
        const values = lines[i].match(/(".*?"|[^",\s]+)(?=\s*,|\s*$)/g) || [];
        
        if (values.length >= 3) {
          try {
            const userData = {
              username: values[0]?.replace(/"/g, '').trim() || '',
              email: values[1]?.replace(/"/g, '').trim() || '',
              role: values[2]?.replace(/"/g, '').trim() || '',
              password: 'password123', // Default password
              available_hours_per_week: parseInt(values[3]?.replace(/"/g, '').trim()) || 40
            };

            // Check if user already exists
            const existingUser = users.find(u => u.username === userData.username);
            if (existingUser) {
              console.log(`User ${userData.username} already exists, skipping...`);
              continue;
            }

            // Create user account only
            await axios.post('http://72.60.101.240:5005/api/users', {
              username: userData.username,
              password: userData.password,
              role: userData.role,
              email: userData.email
            });

            console.log(`Successfully imported user: ${userData.username}`);
          } catch (error) {
            console.error('Error importing user:', error);
          }
        }
      }
      
      fetchUsers();
      alert('Import completed! Check console for details.');
    };
    reader.readAsText(file);
  };

  const downloadSampleCSV = () => {
    const sampleData = [
      ['Username', 'Email', 'Role', 'Available Hours/Week'],
      ['"john.doe"', '"john.doe@company.com"', '"employee"', '"40"'],
      ['"jane.smith"', '"jane.smith@company.com"', '"team_lead"', '"45"'],
      ['"mike.johnson"', '"mike.johnson@company.com"', '"employee"', '"35"']
    ].map(row => row.join(',')).join('\n');

    const blob = new Blob([sampleData], { type: 'text/csv;charset=utf-8;' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'users_sample.csv';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  };

  const filteredUsers = users.filter(user =>
    user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.role.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="users-page">
      <div className="page-header">
        <h1>Users Management</h1>
        <div className="header-actions">
          <input
            type="text"
            placeholder="Search users..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="search-input"
          />
          <button 
            onClick={() => setShowAddForm(true)} 
            className="btn-enterprise btn-primary"
          >
            <span className="btn-icon">➕</span>
            Add User
          </button>
          <button 
            onClick={handleExportUsers} 
            className="btn-enterprise btn-success"
          >
            <span className="btn-icon">📤</span>
            Export CSV
          </button>
          <label className="btn-enterprise btn-info">
            <span className="btn-icon">📥</span>
            Import CSV
            <input
              type="file"
              accept=".csv"
              onChange={handleImportUsers}
              style={{ display: 'none' }}
            />
          </label>
          <button 
            onClick={downloadSampleCSV} 
            className="btn-enterprise btn-warning"
          >
            <span className="btn-icon">📋</span>
            Sample CSV
          </button>
        </div>
      </div>

      {showAddForm && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h2>Add New User</h2>
              <button 
                onClick={() => setShowAddForm(false)} 
                className="close-btn"
              >
                ×
              </button>
            </div>
            <form onSubmit={handleAddUser} className="user-form">
              <div className="form-group">
                <label>Username:</label>
                <input
                  type="text"
                  value={newUser.username}
                  onChange={(e) => setNewUser({...newUser, username: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Password:</label>
                <input
                  type="password"
                  value={newUser.password}
                  onChange={(e) => setNewUser({...newUser, password: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Email:</label>
                <input
                  type="email"
                  value={newUser.email}
                  onChange={(e) => setNewUser({...newUser, email: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Role:</label>
                <select
                  value={newUser.role}
                  onChange={(e) => setNewUser({...newUser, role: e.target.value})}
                  required
                >
                  <option value="">Select Role</option>
                  <option value="manager">Manager</option>
                  <option value="team_lead">Team Lead</option>
                  <option value="employee">Employee</option>
                </select>
              </div>
              <div className="form-group">
                <label>Available Hours per Week:</label>
                <input
                  type="number"
                  min="1"
                  max="80"
                  value={newUser.available_hours_per_week}
                  onChange={(e) => setNewUser({...newUser, available_hours_per_week: parseInt(e.target.value) || 40})}
                  required
                  placeholder="40"
                />
                <small className="form-help">
                  Default weekly working hours for this user (1-80 hours)
                </small>
              </div>
              <div className="form-actions">
                <button type="submit" className="btn-enterprise btn-primary">
                  <span className="btn-icon">✅</span>
                  Add User
                </button>
                <button 
                  type="button" 
                  onClick={() => setShowAddForm(false)} 
                  className="btn-enterprise btn-secondary"
                >
                  <span className="btn-icon">❌</span>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      <div className="users-table-container">
        <table className="users-table">
          <thead>
            <tr>
              <th>Avatar</th>
              <th>Username</th>
              <th>Email</th>
              <th>Role</th>
              <th>Available Hours/Week</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredUsers.map((user) => (
              <tr key={user.id} className="user-row">
                <td>
                  <div className="user-avatar">
                    {user.username.split('').slice(0, 2).join('').toUpperCase()}
                  </div>
                </td>
                <td className="user-username">{user.username}</td>
                <td className="user-email">{user.email}</td>
                <td>
                  <span className={`role-badge role-${user.role.replace('_', '-')}`}>
                    {user.role.replace('_', ' ')}
                  </span>
                </td>
                <td className="hours-cell">
                  <span className="hours-badge">
                    {user.available_hours_per_week || 40}h/week
                  </span>
                </td>
                <td>
                  <div className="user-actions">
                    <button 
                      className="btn-small btn-edit"
                      onClick={() => handleEditUser(user)}
                    >
                      ✏️ Edit
                    </button>
                    <button 
                      className="btn-small btn-delete"
                      onClick={() => handleDeleteUser(user.id)}
                    >
                      🗑️ Delete
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
        {filteredUsers.length === 0 && (
          <div className="no-users">
            <p>No users found. Try adjusting your search or add new users.</p>
          </div>
        )}
      </div>

      {showEditForm && editingUser && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h2>Edit User</h2>
              <button 
                onClick={() => {
                  setShowEditForm(false);
                  setEditingUser(null);
                }} 
                className="close-btn"
              >
                ×
              </button>
            </div>
            <form onSubmit={handleUpdateUser} className="user-form">
              <div className="form-group">
                <label>Username:</label>
                <input
                  type="text"
                  value={editingUser.username}
                  onChange={(e) => setEditingUser({...editingUser, username: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Email:</label>
                <input
                  type="email"
                  value={editingUser.email}
                  onChange={(e) => setEditingUser({...editingUser, email: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Role:</label>
                <select
                  value={editingUser.role}
                  onChange={(e) => setEditingUser({...editingUser, role: e.target.value as 'manager' | 'team_lead' | 'employee'})}
                  required
                >
                  <option value="">Select Role</option>
                  <option value="manager">Manager</option>
                  <option value="team_lead">Team Lead</option>
                  <option value="employee">Employee</option>
                </select>
              </div>
              <div className="form-group">
                <label>Available Hours per Week:</label>
                <input
                  type="number"
                  min="1"
                  max="80"
                  value={editingUser.available_hours_per_week || 40}
                  onChange={(e) => setEditingUser({...editingUser, available_hours_per_week: parseInt(e.target.value) || 40})}
                  required
                  placeholder="40"
                />
                <small className="form-help">
                  Default weekly working hours for this user (1-80 hours)
                </small>
              </div>
              <div className="form-actions">
                <button type="submit" className="btn-enterprise btn-primary">
                  <span className="btn-icon">✅</span>
                  Update User
                </button>
                <button 
                  type="button" 
                  onClick={() => {
                    setShowEditForm(false);
                    setEditingUser(null);
                  }} 
                  className="btn-enterprise btn-secondary"
                >
                  <span className="btn-icon">❌</span>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Users;