import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Project } from '../types';
import '../App.css';

interface ProjectListProps {
  projects: Project[];
  onDeleteProject?: (id: number) => void;
  onManageTeam?: (projectId: number, projectName: string) => void;
  onEditProject?: (project: Project) => void;
  userRole?: string;
  userId?: number;
}

const ProjectList: React.FC<ProjectListProps> = ({ 
  projects, 
  onDeleteProject, 
  onManageTeam, 
  onEditProject,
  userRole, 
  userId 
}) => {
  const navigate = useNavigate();

  const handleProjectDetails = (projectId: number) => {
    if (userRole === 'employee' && userId) {
      navigate(`/project-details/${projectId}`, { 
        state: { userId, projectId } 
      });
    }
  };

  const formatCurrency = (amount?: number) => {
    if (!amount) return 'Not specified';
    return new Intl.NumberFormat('en-IN', {
      style: 'currency',
      currency: 'INR',
    }).format(amount);
  };

  const formatDate = (dateString?: string) => {
    if (!dateString) return 'Not specified';
    return new Date(dateString).toLocaleDateString();
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return '#28a745';
      case 'inactive': return '#ffc107';
      case 'completed': return '#6c757d';
      case 'dropped': return '#dc3545';
      default: return '#6c757d';
    }
  };

  return (
    <div className="users-table-container">
      <table className="users-table">
        <thead>
          <tr>
            <th>Project</th>
            <th>Status</th>
            {userRole !== 'employee' && <th>Budget</th>}
            <th>{userRole === 'employee' ? 'My Hours/Week' : 'Estimated Hours'}</th>
            <th>Start Date</th>
            <th>End Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          {projects.map((project) => (
            <tr key={project.id} className="user-row">
              <td>
                <div className="project-info">
                  <div 
                    className={`project-name ${userRole === 'employee' ? 'clickable' : ''}`}
                    onClick={() => userRole === 'employee' && handleProjectDetails(project.id)}
                    style={{ cursor: userRole === 'employee' ? 'pointer' : 'default' }}
                  >
                    {project.name}
                  </div>
                  {project.description && (
                    <div className="project-description">{project.description}</div>
                  )}
                </div>
              </td>
              <td>
                <span 
                  className={`role-badge status-${project.status}`}
                  style={{ backgroundColor: getStatusColor(project.status) }}
                >
                  {project.status.charAt(0).toUpperCase() + project.status.slice(1)}
                </span>
              </td>
              {userRole !== 'employee' && (
                <td className="budget-cell">{formatCurrency(project.budget)}</td>
              )}
              <td className="hours-cell">
                {userRole === 'employee' 
                  ? (project as any).allocated_hours_per_week || 'Not specified'
                  : project.estimated_hours || 'Not specified'
                }
              </td>
              <td className="date-cell">{formatDate(project.start_date)}</td>
              <td className="date-cell">{formatDate(project.end_date)}</td>
              <td>
                <div className="user-actions">
                  {userRole === 'employee' ? (
                    <button 
                      className="btn-small btn-primary"
                      onClick={() => handleProjectDetails(project.id)}
                      title="View project details"
                    >
                      📋 View Details
                    </button>
                  ) : (
                    <>
                      {onEditProject && userRole === 'manager' && (
                        <button 
                          className="btn-small btn-primary"
                          onClick={() => onEditProject(project)}
                          title="Edit project"
                        >
                          ✏️ Edit
                        </button>
                      )}
                      {onManageTeam && (
                        <button 
                          className="btn-small btn-edit"
                          onClick={() => onManageTeam(project.id, project.name)}
                          title="Manage team members"
                        >
                          👥 Manage Team
                        </button>
                      )}
                      {onDeleteProject && (
                        <button 
                          className="btn-small btn-delete"
                          onClick={() => onDeleteProject(project.id)}
                          title="Delete project"
                        >
                          🗑️ Delete
                        </button>
                      )}
                    </>
                  )}
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ProjectList;
