import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { projectAPI, userAPI } from '../services/api';
import { Project } from '../types';
import ProjectList from './ProjectList';
import AddProject from './AddProject';
import EditProject from './EditProject';
import '../App.css';

interface ProjectsProps {
  user?: any;
}

const Projects: React.FC<ProjectsProps> = ({ user }) => {
  const navigate = useNavigate();
  const [projects, setProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showAddProject, setShowAddProject] = useState(false);
  const [editingProject, setEditingProject] = useState<Project | null>(null);

  useEffect(() => {
    fetchProjects();
  }, []);

  const fetchProjects = async () => {
    try {
      setLoading(true);
      let projectData;
      
      if (user?.role === 'employee') {
        // For employees, fetch only their assigned projects
        projectData = await userAPI.getUserProjects(user.id);
      } else {
        // For managers and team leads, fetch all projects
        projectData = await projectAPI.getAll();
      }
      
      setProjects(projectData);
      setError('');
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to fetch projects');
    } finally {
      setLoading(false);
    }
  };

  const handleProjectAdded = () => {
    fetchProjects(); // Refresh the project list
  };

  const handleProjectUpdated = () => {
    fetchProjects(); // Refresh the project list
    setEditingProject(null);
  };

  const handleEditProject = (project: Project) => {
    setEditingProject(project);
  };

  const handleDeleteProject = async (id: number) => {
    if (window.confirm('Are you sure you want to delete this project?')) {
      try {
        await projectAPI.delete(id);
        fetchProjects(); // Refresh the project list
      } catch (err) {
        alert(err instanceof Error ? err.message : 'Failed to delete project');
      }
    }
  };

  const handleManageTeam = (projectId: number, projectName: string) => {
    navigate(`/team-management/${projectId}`, { 
      state: { projectName } 
    });
  };

  if (loading) {
    return (
      <div className="page-container">
        <div className="loading">Loading projects...</div>
      </div>
    );
  }

  return (
    <div className="users-page">
      <div className="page-header">
        <h1>{user?.role === 'employee' ? 'My Projects' : 'Project Management'}</h1>
        {user?.role !== 'employee' && (
          <div className="header-actions">
            <button 
              onClick={() => setShowAddProject(true)} 
              className="btn-enterprise btn-primary"
            >
              <span className="btn-icon">➕</span>
              Add Project
            </button>
          </div>
        )}
      </div>

      {error && (
        <div className="error-message">
          {error}
        </div>
      )}

      <div className="projects-content">
        {projects.length === 0 ? (
          <div className="empty-state">
            <h3>No projects found</h3>
            <p>{user?.role === 'employee' ? 'You are not assigned to any projects yet.' : 'Get started by creating your first project.'}</p>
            {user?.role !== 'employee' && (
              <button 
                className="btn-enterprise btn-primary"
                onClick={() => setShowAddProject(true)}
              >
                <span className="btn-icon">➕</span>
                Create Project
              </button>
            )}
          </div>
        ) : (
          <ProjectList 
            projects={projects} 
            onDeleteProject={user?.role !== 'employee' ? handleDeleteProject : undefined}
            onManageTeam={user?.role !== 'employee' ? handleManageTeam : undefined}
            onEditProject={user?.role === 'manager' ? handleEditProject : undefined}
            userRole={user?.role}
            userId={user?.id}
          />
        )}
      </div>

      {showAddProject && (
        <AddProject
          onProjectAdded={handleProjectAdded}
          onClose={() => setShowAddProject(false)}
        />
      )}

      {editingProject && (
        <EditProject
          project={editingProject}
          onProjectUpdated={handleProjectUpdated}
          onClose={() => setEditingProject(null)}
        />
      )}
    </div>
  );
};

export default Projects;
